.proceed_btn_div {
  padding:0 1.25rem;
 
}
.proceed-btn {
  display: flex;
  background-color: #29529F;
  padding: 5px 1.25rem;
  align-items: center;
  justify-content: center;
  border-radius: 20px;
  cursor: pointer;
  border: 1px solid #29529F;
    box-sizing: border-box;
  box-shadow: 0px 8px 10px rgba(0, 0, 0, 0.25);
}
.proceed_text {
  font-size:25px;
  font-weight: 400;
  color: #ffffff;
}